.App {
  text-align: center;
}
div{
  box-sizing:border-box;
}
body{
  font-family:'nanumsquare';
  font-weight:600;
}

.modal{
  padding: 20px;
  padding-left:20px;
  background:#eee;
  margin-top:20px;
  text-align:left;
}
.title{
  padding: 20px;
  background:#61dafb;
  margin-top:20px;
  text-align:left;
}
.black-nav{
  background:black;
  width:100%;
  display:flex;
  color:white;
  padding:20px;
  font-weight:600;
  font-size:20px;
}

.list{
  margin-top:30px;
  text-align:left;
  padding-left:20px;
  border-bottom: 1px solid grey;
}

.list h3{
  cursor: pointer;
}

.list span{
  padding-left:10px;
}

.like{
  cursor: pointer;
}


.remove{
  font-size:15px;
  font-weight: 600;
  color:black;
  border-radius: 5px;
  border:1px solid black;
  background-color: lightgray;
  margin-bottom:10px;
  font-family:'nanumsquare';
  cursor: pointer;
}

.write{
  font-size:15px;
  font-weight:600;
  color:mintcream;
  background-color: cadetblue;
  border-radius: 10px;
  border:0.5px solid cadetblue;
  font-family:'nanumsquare';
  width: 50px;
  height:25px;
  margin-top:20px;
  margin-left:10px;
  cursor: pointer;
}

.area{
  width:200px;
  height:20px;
  border:2px solid black;
}